import React from 'react';
import styled, { css } from 'styled-components'
import { colorGrey } from '../../styles';

const Container = styled.div`
  position: fixed;
  background-color: ${props => props.background || colorGrey};
  color: ${props => props.color || '#fff'};
  ${props => css`
    top: ${props.top};
    right: ${props.right};
    bottom: ${props.bottom};
    left: ${props.left};
    width: ${props.width}px;
    height: ${props.height}px;
  `}
  border-radius: ${props => props.round && '100%'};
  transform: translateY(${props => props.corner && "-50%, -50%"});
`
const FixedInfoButton = ({
  top, right, bottom, left, background, color, height, width, round
}) => {
  const dims = { top, right, bottom, left, height, width }
  const styles = { background, color, round }
  return (
    <Container
      {...dims}
      {...styles}
    >
      Fixed Button
    </Container>
  )
}

export default FixedInfoButton